@import "../../css-config/mixins.scss";

  .joblist {
    width: 60%;
    padding: 2rem;
    display: flex;
    justify-content: center;
  margin-bottom: 1.3rem;
    background: #fff;
    box-shadow: 0px 0px 22px 2px rgba(255, 32, 32, 0.25);
    .logo {
      width: 10%;
      img {
        width: 60%;
      }
    }
    .content {
      width: 90%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .left {
        width: 30%;
        .left-one {
          p {
            font-size: 14px;
            font-weight: bold;
          }
        }
        .left-two {
          display: flex;
          justify-content: flex-start;
          align-items: center;
          gap: 15px;
          margin-top: 8px;
          p {
            font-size: 12px;
            font-weight: bold;
            color: #acacac;
            display: flex;
            align-items: center;
            #group {
              width: 15px;
              margin-right: 5px;
            }
            #vector {
              width: 7px;
              margin-right: 5px;
            }
            #flag {
              width: 17px;
              margin-right: 5px;
            }
          }
        }
        .left-three {
          display: flex;
          justify-content: flex-start;
          gap: 20px;
          align-items: center;
          margin-top: 10px;
          p {
            font-size: 12px;
            font-weight: bold;
            color: #ed5353;
          }
        }
      }
      .right {
        width: 65%;
        display: flex;
        flex-direction: column;
        gap: 14px;
        .skills {
          display: flex;
          justify-content: flex-end;
          align-items: center;
          gap: 15px;
          overflow: hidden;
          width: 100%;
          p {
            // flex: 1;
            font-size: 12px;
            border-radius: 1.433px;
            background: #fee;
            padding: 4px 9px;
            font-weight: bold;
          }
        }
        .buttons {
          display: flex;
          justify-content: flex-end;
          gap: 1rem;
  
          #view {
            background: #ed5353;
            color: white;
            border-radius: 3.922px;
          outline: none;
            border: none;
            padding: 5px 13px;
            cursor: pointer;
          }
          #edit {
            background: white;
            color: #ed5353;
            border-radius: 3.922px;
            border: 2px solid #ed5353;
            outline: none;
            padding: 5px 27px;
            cursor: pointer;
          }
        }
      }
    }
  }

